
import { defineComponent, reactive } from "vue";

import { PAGE_SIZE } from "@/shared/constants/common.const";
import { IBService } from "@/shared/type/COM214002SC00.type";
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import { ConvertCode } from '@/shared/services/ConvertCodeService';
const convertCode = new ConvertCode();
export default defineComponent({
  name: "com214002sc00",
  components: {
    Pagination,
    BreadCrumb,
    SelectOption
  },

  data() {
    const dataTable: IBService[] = [];
    const tmpDataTable: IBService[] = [];
    const input = reactive({
      serviceID: '',
      serviceIDDescription: '',
      serviceIDName: '',
      transactionControlStatusCode: '',
      checkServiceYN: ''
    })
    const header = [
      { columnName: this.$t('com214002sc00.column.no') },
      { columnName: this.$t('com214002sc00.column.view') },
      { columnName: this.$t('com214002sc00.column.service_id') },
      { columnName: this.$t('com214002sc00.column.service_name') },
      { columnName: this.$t('com214002sc00.column.description') },
      { columnName: this.$t('com214002sc00.column.status') },
      { columnName: 'Check Service YN' },
    ];
    return {
      convertCode,
      input,
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      isReset: false,
      dataTable,
      tmpDataTable,
      header,
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'com214002sc00.ib_management', keyPath: '', type: 'label' },
        { value: 'com214002sc00.ib_service', keyPath: '', type: 'label' },
        { value: 'com214002sc00.ib_service_list', keyPath: '', type: 'route' },
      ]
    };
  },
  created() {
    this.requestTrData();
  },

  computed:{
    statusCodeValue() {
      return (statusCode: string) => `${statusCode}` +' : '+ this.$codeConverter.convertValue('screenStatusCode', `${statusCode}`);
    },
    YnType () {
      return this.$store.state.CommonCodeModule.commonCode.YnType
    }
  },

  methods: {
    statusConverter(statusCode): string {
      console.log('statusCode', statusCode);
      switch (statusCode) {
        case '01: Active':
          return 'success'
        case '02: Inactive':
          return 'fail'
        default:
          return '03: info'
      }
    },

    onMappingTableData(lists) {
      const { BusinessCommonStatusCode } = this.$store.state.CommonCodeModule.commonCode;
      return lists.map((item, index) => ({
        No: index + 1,
        serviceID: item.serviceID,
        serviceIDName: item.serviceIDName,
        serviceIDDescription: item.serviceIDDescription,
        transactionControlStatusCode: BusinessCommonStatusCode && item.transactionControlStatusCode ? `${BusinessCommonStatusCode.find((type) => type.key === item.transactionControlStatusCode)?.key}: ${BusinessCommonStatusCode.find((type) => type.key === item.transactionControlStatusCode)?.value}` : '',
        checkServiceYN: item.checkServiceYN
      }));
    },
    // Get Data List
    async requestTrData(mode?: string) {
      const body = {
        // pageNumber: this.currentPage,
        pageNumber: mode == 'search' ? 0 : this.currentPage,
        pageSize: this.pageSize,
        serviceIDName: this.input.serviceIDName,
        serviceID: this.input.serviceID,
        transactionControlStatusCode: this.input.transactionControlStatusCode,
        checkServiceYN: this.input.checkServiceYN
      };
      if (mode === 'search') {
        this.isReset = true;
        this.currentPage = 1
      }
      const res: any = await this.$request.getRequest('COM21400211', body, false);
      if (res.header.result) {
        this.dataTable = this.onMappingTableData(res.body.list)
        this.tmpDataTable = res.body.list;
        this.totalItems = res.body.totalCount;
      }
    },

    async handleOnClick(onCLick: string, rowData) {
      if (onCLick === "clear") {
        this.currentPage = 1
        this.isReset = true;
        this.input.serviceID = "";
        this.input.transactionControlStatusCode = "";
        this.input.serviceIDName = "";
        this.input.checkServiceYN = "",
        await this.requestTrData();
      } else if (onCLick === "register") {
        this.$router.push({
          path: '/COM214002SC01',
          query: {
            type: 'register'
          }
        })
        // this.$router.push(`/common-management/ib-service-id/${type}`);
      } else {
        this.$router.push({
          path: '/COM214002SC01',
          query: {
            type: 'edit',
            serviceID: rowData.serviceID
          }
        })
        // this.$router.push(`/common-management/ib-service-id/${type}/${rowData?.serviceID}`);
      }
    },

    // Search List 
    // async handleSearch() {
    //   const body = {
    //     pageNumber: this.currentPage,
    //     pageSize: this.pageSize,
    //     serviceIDName: this.inputServiceId,
    //     serviceID: this.inputServiceId,
    //     transactionControlStatusCode: this.inputServiceId
    //   };
    //   const status = convertCode.convertValue('businessCommonStatusCode', this.selectedStatus);
    //   const res: any = await request.getRequest("COM21400211", body, false);
    //   if (res.header.result) {
    //     if (!this.inputServiceId && status === "All") {
    //       return this.dataTable = this.tmpDataTable;
    //     } else {
    //       const searchResult: IBService[] = this.tmpDataTable.filter((searchValue: IBService) => {
    //         const searchServiceID = searchValue.serviceID.toLowerCase().includes(this.inputServiceId.toLowerCase());
    //         const searchServiceName = searchValue.serviceIDName.toLowerCase().includes(this.inputServiceId.toLowerCase());

    //         if (this.inputServiceId && status === "All") {
    //           if ((searchServiceID || searchServiceName))
    //             return searchValue;

    //         } else if (this.inputServiceId && status !== "All") {
    //           if ((searchServiceID || searchServiceName) && (status === searchValue.transactionControlStatusCode))
    //             return searchValue;

    //         } else if (!this.inputServiceId && status === "All") {
    //           return searchValue;

    //         } else if (!this.inputServiceId && status !== "All") {
    //           if (status === searchValue.transactionControlStatusCode)
    //             return searchValue;
    //         }
    //       })
    //       this.dataTable = searchResult;
    //     }
    //   }
    // },
    handlePagination(pageNumber: number) {
      this.isReset = false;
      this.currentPage = pageNumber
      this.requestTrData();
    },
    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex
    }
  },
});
